/* Footer */

	#footer {
		@include vendor('transition', 'opacity 0.5s ease-in-out');
		bottom: 4em;
		color: _palette(fg-light);
		left: 4em;
		opacity: 0.5;
		position: absolute;

		.icons {
			margin: 0 0 (_size(element-margin) * 0.25) 0;
		}

		.copyright {
			font-size: 0.8em;
			list-style: none;
			padding: 0;

			li {
				border-left: solid 1px _palette(border2);
				display: inline-block;
				line-height: 1em;
				margin: 0 0 0 0.75em;
				padding: 0 0 0 0.75em;

				&:first-child {
					border-left: 0;
					margin-left: 0;
					padding-left: 0;
				}
			}

			a {
				color: inherit;
			}
		}

		&:hover {
			opacity: 1;
		}

		> :last-child {
			margin-bottom: 0;
		}

		@include breakpoint('<=xlarge') {
			bottom: 3.5em;
			left: 3.5em;
		}

		@include breakpoint('<=small') {
			bottom: 2em;
			left: 2em;
		}

	@include breakpoint('<=xxsmall') {
		bottom: 1.25em;
		left: 1.25em;
		right: 1.25em;
		text-align: center;
		
		.icons {
			li {
				display: inline-block;
				margin: 0 0.5em;
			}
		}
		
		.copyright {
			font-size: 0.7em;
			li {
				display: block;
				border-left: none;
				margin: 0.5em 0 0 0;
				padding: 0;
			}
		}
	}

	@include breakpoint('short') {
		bottom: auto;
		left: auto;
		margin: (_size(element-margin) * 0.5) 0 0 0;
		position: relative;
		text-align: center;
	}
	}